#xbody {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    background:rgb(255,255,255);
    background-size: 100vh;
    background-attachment: fixed;
    text-align: center;
}
.imagen{

    height:150px;
    background: url('http://localhost:3000/images/logos/it_logo.png');
    text-align: center;
    width:400px;
}
.imagen_wrapper{
    margin-top:20px;

}
* {
    box-sizing: border-box;
}
.contenedor{
    width: 100%;
    padding: 15px;
}
.formulario{
    background: #fff;
    padding: 3px;
}
h1{
    text-align: center;
    color: #1a2537;
    font-size: 40px;
}
input[type="text"],
input[type="password"]{
    font-size: 20px;
    width: 82%;
    padding: 10px;
    border: none;
}

#in_login_captcha{
    margin-bottom: 10px;
    margin-top: -10px;
    padding: 8px 130px;
    border: 1px solid #aaa;
    height: 30px;
    width: 100%;
}
.input-contenedor{
    margin-bottom: 15px;
    border: 1px solid #aaa;
}
.icon{
    min-width: 50px;
    text-align: center;
    color: #999;
}
.button{
    border: none;
    width: 100%;
    color: white;
    font-size: 20px;
    background:  #1a2537;
    padding: 15px 20px;
    border-radius: 5px;
    cursor: pointer;
}
.button:hover{
    background: cadetblue;
}
p{
    text-align: center;
}
.link{
    text-decoration: none;
      color: #1a2537;
    font-weight: 600;
}
.link:hover{
     color: cadetblue;
}
@media(min-width:768px)
{
    .formulario{
        margin: auto;
        width: 500px;

        border-radius: 2%;
    }
}




























